// bg
.bg-transparent{
  background-color: transparent;
}

.bg-cover {
  background-position: center center;
  background-size: cover;
}
//logo
.hide-text {
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap;
}

//btn 

.btn{
  //Structure pure css core 
  display: inline-block;
  line-height: normal;
  white-space: nowrap;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  -webkit-user-drag: none;
  user-select: none;
  // 全網站設定已經有了
  // box-sizing: border-box;

  //
  padding: {
    top    : pxToRem(16px);
    right  : pxToRem(58px);
    bottom : pxToRem(16px);
    left   : pxToRem(58px);
  };  
  border: none;
  &:hover {
    background-color: $color-secondary;
  }
}

.btn-primary{
  color: #fff;
  background-color: $color-primary;
}

// block
.d-block{
  display: block;
}
// bg
// flex
.d-flex {
  display: flex;
}
.flex-column {
  flex-direction: column;
}
.justify-content-between {
  justify-content: space-between;
}
.justify-content-end {
  justify-content: flex-end;
}
.justify-content-center {
  justify-content: center;
}
.align-items-center {
  align-items: center;
}

.flex-center{
  justify-content: center;
  align-items: center;
}
//bg
.bg-primary {
  background-color: $color-primary;
}
.bg-secondary {
  background-color: $color-lighter;
}

//text
.text-center {
  text-align: center;
}

.text-white {
  color: $text-white;
}
