*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  // display: block;
}

body {
  font-size: 16px;
  line-height: $base-line-height;
  font-family: $body-font-family;
}
$container-width: 1200px;

.container {
  max-width: pxToRem($container-width);
  margin: {
    top: 0;
    right: auto;
    bottom: 0;
    left: auto;
  }
  @include pad-below {
    max-width: pxToRem(672px);
    padding: {
      left: pxToRem(48px);
      right: pxToRem(48px);
    }
  }
}
// 因為寫很多了直接全部都加上
a {
  @extend %btn;
}
