
@mixin desktop-below {
  @media (max-width: 1023px) {
    @content;
  }
}
@mixin pad-below {
  @media (max-width: 767px) {
    @content;
  }
}
@mixin mobile {
  @media (max-width: 568px) {
    @content;
  }
}

@mixin positionAB($top: auto, $right: auto, $bottom: auto, $left: auto) {
  position: absolute;
  @if $top != auto {
    top: $top;
  }
  @if $right != auto {
    right: $right;
  }
  @if $bottom != auto {
    bottom: $bottom;
  }
  @if $left != auto {
    left: $left;
  }
}

