@import "./utils/function";
@import "./config/variables";
@import "./utils/mixin";
@import "./utils/extend";

// reset
@import "./config/reset";
@import "./config/base";
@import "./config/typography";

@import "./utils/helper";
@import "./utils/spacing";


// page

$color: #00cc99;

.header {
  padding: {
    left: 88px;
    right: 56px;
  };
  .logo{
    width: 80px;
    height: 49px;
    margin: {
      top: 30px;
      bottom: 30px;
    };
  }
  .navMenu{
    >li a{
      font-size: 20px;
      padding: {
        top: 39px;
        bottom: 39px;
      };
      &:hover{
        background-color: $color-secondary;
      }
    }
  }
}

.footer {
  height: 200px;
  background-color: #00cc99;
}

.box {
  width: 100px;
  height: 100px;
  background-color: red;
  display: flex;
  transition: all 0.5s;
  &:hover {
    width: 200px;
    background-color: $color;
  }
}

