%btn {
  text-decoration: none;
  display: block;
  color: $color-white;
}

%bg-cover {
  background-position: center center;
  background-size: cover;
}

%positionABCenter{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
// extend 在哪個位子沒有差，因為都會提出就是看他放的位子
// %name 這個是佔位符 如果用 class .name 這個會多產出一個 class
// 選用時機找出重複三次的
// 可思考和 functional css 比較 是要在SASS 內改，還是直接掛在 HTML 的 class 上面
.extendExample {
  // @extend bg-cover;
  background: #000;
  @extend %bg-cover;
}
